/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { IconButton, Snackbar } from '@safe-wordpress/components';
import { useEffect, useState } from '@safe-wordpress/element';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import type { SiteId } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';

export type PageTitleProps = {
	readonly isSubscribed: boolean;
	readonly siteId: SiteId;
};

export const PageTitle = ( {
	isSubscribed,
	siteId,
}: PageTitleProps ): JSX.Element => {
	const [ copied, setCopied ] = useState( false );

	useEffect( () => {
		const timeout = setTimeout( () => setCopied( false ), 1500 );
		return () => clearTimeout( timeout );
	}, [ copied ] );

	return (
		<h1 className="wp-heading-inline nelio-ab-testing-page-title">
			<span>
				{ isSubscribed
					? _x( 'Account Details', 'text', 'nelio-ab-testing' )
					: _x(
							'Upgrade to Nelio A/B Testing Premium',
							'user',
							'nelio-ab-testing'
					  ) }
			</span>
			<span className="nelio-ab-testing-page-title__support-key">
				<strong>
					{ _x( 'Support Key:', 'text', 'nelio-ab-testing' ) }
				</strong>
				<code>{ siteId }</code>
				{ !! navigator.clipboard?.writeText && (
					<IconButton
						icon="admin-page"
						onClick={ () =>
							! copied &&
							void navigator.clipboard
								.writeText( siteId )
								.then( () => setCopied( true ) )
						}
					/>
				) }
			</span>

			{ copied && (
				<Snackbar className="nelio-ab-testing-page-title__support-key-copied">
					{ _x(
						'Copied!',
						'text (support key)',
						'nelio-ab-testing'
					) }
				</Snackbar>
			) }
		</h1>
	);
};
